var that;
export default {
	name: 'scroll',
	props: {
		type: {
			type: String,
			default: '1'
		}
	},
	data() {
		return {
			refresh: false
		};
	},
	computed: {},
	methods: {
		downrefresh() {
			this.$emit('downrefresh');
			that.refresh = true;
			setTimeout(res => {
				that.refresh = false;
			}, 10000);
		},
		stop() {
			that.refresh = false;
		},
		restore() {
			console.log('复位');
		}
	},
	created() {
		that = this;
	}
};
